import lModal from '@/components/l-modal/l-modal.vue'
export default {
	components: {
		lModal,
	},
	data() {
		return {
			userLogin: true, // 用户是否登录
			isVip: true, // 是否是vip
			isShowModal: false,
			modalTitle: '提示',
			memberId: '',
			userInfo: {},
			code: 0
		}
	},
	onShow(){
		this.memberId = uni.getStorageSync('userInfo') ? JSON.parse(uni.getStorageSync('userInfo')).id : ''
		this.getMember()
	},
	onPullDownRefresh() {
		this.userInfo = {};
		uni.showLoading({
			title: '加载中'
		});
		this.getMember()
		uni.hideLoading();
		uni.stopPullDownRefresh()
	},
	methods: {
		// 获取我的信息
		getMember(){
			this.$Request.get(`/appMemberController.do?getMember&memberId=${this.memberId}`)
			.then(res => {
				this.code = res.code
				this.userInfo = res.data.memberVo ? res.data.memberVo : ''
				if(res.code == 0){
					this.userLogin = true
					uni.setStorageSync('userInfo', JSON.stringify(res.data.memberVo))
				}else if(res.code == '100'){
					this.userLogin = false
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		gotoUrl(url){
			let vm = this
			if(vm.code == 0){
				uni.navigateTo({
					url:url
				})
			}else if(vm.code == 100){
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		// 点击登录按钮
		gotoLogin(url){
			uni.navigateTo({
				url:url
			})
		},
		gotoTab(url){
			let vm = this
			if(vm.code == 0){
				uni.switchTab({
					url: url
				})
			}else if(vm.code == 100){
				uni.showModal({
				    title: '提示',
				    content: '您尚未登录，是否去登录？',
				    success: function (res) {
				        if (res.confirm) {
				            uni.navigateTo({
				            	url:'/pages/loginAll/login'
				            })
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});
			}
		},
		gotoReturn(){
			this.isShowModal = true
		},
		cancel(){
			this.isShowModal = false
		},
		confirm() {
			this.isShowModal = false
			uni.clearStorage();
			uni.showToast({
				icon: 'success',
				title: '退出登录成功！',
				duration: 2000
			})
			setTimeout(() => {
				uni.navigateTo({
					url: '/pages/myData/loadPage'
				})
			}, 1500)
		},
	}
}
